body {
    margin: 0;
    padding: 0;
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    font-family: 'Trebuchet MS', Arial, sans-serif;
}

.app {
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.05) 0 6px 24px, rgba(0, 0, 0, 0.08) 0 0 0 1px;
    border-radius: 10px;
    padding: 10px;
    width: min(800px, 90vw);
    margin-top: 50px;
    margin-bottom: 50px;
    box-sizing: border-box;
}

.button-container, .info-container {
    display: flex;
    align-items: center;
}

.info-container {
    flex-grow: 1;
    padding: 10px 16px;
    gap: 16px;
}

.button-container {
    justify-content: center;
}

@media only screen and (max-width: 720px) {
    .info-container {
        flex-direction: column;
        justify-content: space-between;
        gap: 0;
    }
}

@media only screen and (max-width: 430px) {
    .list-item {
        flex-direction: column;
        padding-bottom: 10px;
        gap: 0;
    }
}

.button-container button {
    margin: 0 5px;
    padding: 5px 10px;
    font-size: 10px;
    border-radius: 12px;
    background-color: transparent;
}

.edit {
    border: 1.5px solid rgb(141, 181, 145);
    color: rgb(141, 181, 145);
    text-transform: uppercase;
}

.edit:hover {
    background-color: rgb(182, 223, 186);
}

.edit:active {
    color: rgb(255, 255, 255);
    background-color: rgb(141, 181, 145);
}

.delete {
    border: 1.5px solid rgb(255, 175, 163);
    color: rgb(255, 175, 163);
    text-transform: uppercase;
}

.delete:hover {
    background-color: rgb(255, 201, 193);
}

.delete:active {
    color: rgb(255, 255, 255);
    background-color: rgb(255, 175, 163);
}

.create {
    border: 1.5px solid rgb(108, 115, 148);
    color: rgb(108, 115, 148);
    text-transform: uppercase;
}

.create:hover {
    background-color: rgb(131, 138, 172);
}

.create:active {
    color: rgb(255, 255, 255);
    background-color: rgb(108, 115, 148);
}

button.signout {
    border: 1.5px solid rgb(255, 175, 163);
    background-color: rgb(255, 175, 163);
    color: rgb(234, 234, 234);
    text-transform: uppercase;
}

.signout:hover {
    background-color: rgb(175, 94, 94);
    border: 1.5px solid rgb(175, 94, 94);
}

.signout:active {
    border: 1.5px solid rgb(175, 81, 81);
    color: rgb(255, 255, 255);
    background-color: rgb(175, 81, 81);
}

input {
    margin: 11px 0;
    padding: 12px 16px;
    border-radius: 12px;
    border: 1.5px solid rgb(230, 232, 236);

}

/* List Header */

.list-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: rgba(0, 0, 0, 0.05) 1px solid;
}

/* List Item */

.list-item {
    width: 100%;
    margin: 10px 0;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.08) 0 0 0 1px;
    display: flex;
    justify-content: space-between;
}

.tick {
    padding: 10px;
}

.tick path {
    fill:none;
    stroke:#000;
    stroke-miterlimit: 10;
    stroke-width: 32px;

}

/* Modal*/

.overlay {
    position: absolute;
    left:0;
    top:0;
    width:100vw;
    height:100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    width: min(500px, 90vw);
    background-color: rgb(255, 255, 255);
    padding: 40px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.05) 0 6px 24px 0, rgba(0, 0, 0, 0.08) 0 0 0 1px;
    box-sizing: border-box;
}

.modal .form-title-container {
    display: flex;
    justify-content: space-between;
}

.modal .form-title-container button {
    border: none;
    background-color: transparent;
}

.modal .form-title-container button:active {
    color: rgb(255, 50, 50);
}

.modal form {
    display: flex;
    flex-direction: column;
}

.modal label {
    font-size: 13px;
}

.modal input#range {
    padding: 12px 0px;
}

/* Auth */
.auth-container {
    display: flex;
    justify-content: center;
    margin: 5px;
}

.auth-container-box {
    width: 500px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.05) 0 6px 24px, rgba(0, 0, 0, 0.08) 0 0 0 1px;
}

.auth-container-box form {
    display: flex;
    flex-direction: column;
    min-height: 350px;
    padding: 20px;
}

.auth-options {
    display: flex;
}

.auth-options button {
    width: 50%;
    border: none;
    padding: 10px;
    color: rgb(35, 38, 47);

}

.copyright {
    font-size: 13px;
    margin-top: 10px;
    color: rgb(114, 114, 144);
    text-align: center;
}

.copyright::before {
    content: 'Â© ';
}

.greeting {
    float: right;
    font-size: 13px;
    margin-top: 10px;
    color: rgb(114, 114, 144);
}

.outer-bar {
    width:200px;
    height: 14px;
    background-color: rgb(216, 216, 216);
    border-radius: 7px;
    overflow: hidden;
    /* margin: 10px auto; */
}

.inner-bar {
    height: 14px;
}

.task-title {
    flex-basis: 50%;
    flex-grow: 1;
}

.tiptool {
    position:absolute;
    top: -50%;
    left: 5px;
    z-index:100;
    background-color: rgba(0, 150, 199, 0.2);
    border-radius: 10px;
    /* border: solid #0096C7; */
    padding: 10px 10px;
    text-align: center;
}

.tiptool::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: rgba(0, 150, 199, 0.2) transparent transparent transparent;
}